:host {
    display: block;
    -webkit-user-select: none;
    user-select: none;
}

.list-group-item.active .form-check:not(.form-switch) .form-check-input[type="checkbox"]:checked {
    background: #fff !important;
    border-color: #fff !important;
}

.list-group-item.active .form-check:not(.form-switch) .form-check-input[type="checkbox"]::after {
    color: #e91e63 !important;
}

.list-group-item .form-check {
    margin-bottom: 0 !important;
}