import { Component, Element, Event, EventEmitter, Host, Listen, Method, Prop, forceUpdate, h } from '@stencil/core';
import { FnIcon } from '../../../utils/functional/functional';
import { appendHighlight, debounce, getFieldValue, setFieldValue } from '../../../utils/utils';
import { StormTreeView } from '../storm-tree-view';

@Component({
  tag: 'storm-tree-node',
  styleUrl: 'storm-tree-node.css',
  shadow: false,
})
export class StormTreeNode {
  private _child: HTMLStormTreeNodeElement[];

  @Element() host: HTMLElement;
  @Prop({ mutable: true }) href: string = 'javascript:void(0);';
  @Prop() dataSet: object;
  @Prop() tree: StormTreeView;
  @Prop() parent?: StormTreeNode;
  @Prop() content: string;
  @Prop() searchText?: string;

  @Event() nodeSelect: EventEmitter<string>;
  @Event() nodeUnselect: EventEmitter<string>;
  @Event() nodeClick: EventEmitter<string>;
  @Event() nodeDblClick: EventEmitter<string>;
  @Event() nodeCheck: EventEmitter<string>;
  @Event() nodeUncheck: EventEmitter<string>;

  @Listen('nodeClick')
  async handleNodeClick(ev: CustomEvent<string>) {
    if (ev.detail !== this.getId()) return;

    if (this.getSelectable()) {
      if (!this.getSelected()) {
        await this.select();
      } else if (this.tree.nullable) {
        await this.unselect();
      }
    } else {
      ev.stopPropagation();
      if (this.hasChild()) {
        this.expandToggle();
      }
    }
  }

  expandToggle() {
    this.getExpanded() ? this.collapse() : this.expand();
  }

  getId(): string {
    return getFieldValue(this.dataSet, this.tree.fieldId) as string;
  }

  getHref(): string {
    const href = getFieldValue(this.dataSet, this.tree.fieldHref) as string;
    return href ?? this.href;
  }

  getChild(): object[] {
    return (getFieldValue(this.dataSet, this.tree.fieldChildren) as object[]) || [];
  }

  getSelectable(): boolean {
    const selectable = getFieldValue(this.dataSet, this.tree.fieldSelectable) as boolean;
    return selectable ?? this.tree.selection != 'none';
  }

  getCheckSelectable(): boolean {
    return getFieldValue(this.dataSet, this.tree.fieldCheckSelectable) === true;
  }

  getSelected(): boolean {
    return getFieldValue(this.dataSet, this.tree.fieldSelected) === true;
  }

  getChecked(): boolean {
    return getFieldValue(this.dataSet, this.tree.fieldChecked) === true;
  }

  getDisabled(): boolean {
    return getFieldValue(this.dataSet, this.tree.fieldDisabled) === true;
  }

  setSelected(value: boolean) {
    if ((getFieldValue(this.dataSet, this.tree.fieldSelected) as boolean) === value) return;
    setFieldValue(this.dataSet, this.tree.fieldSelected, value);
    this.rerender();
  }

  setChecked(value: boolean) {
    if ((getFieldValue(this.dataSet, this.tree.fieldChecked) as boolean) === value) return;
    setFieldValue(this.dataSet, this.tree.fieldChecked, value);
    this.rerender();
  }

  getExpanded(): boolean {
    return getFieldValue(this.dataSet, this.tree.fieldExpanded) === true;
  }

  async setExpanded(value: boolean): Promise<void> {
    if (this.getExpanded() === value) return;
    setFieldValue(this.dataSet, this.tree.fieldExpanded, value);

    if (value && !(getFieldValue(this.dataSet, this.tree.fieldChildLoaded) as boolean)) {
      await this.tree.loadChild(this);
    }

    this.rerender();
  }

  private readonly rerender = debounce(() => {
    forceUpdate(this);
  }, 50);

  @Method()
  async select() {
    if (this.getSelected()) {
      return;
    }
    this.setSelected(true);
    this.nodeSelect.emit(this.getId());
  }

  @Method()
  async unselect() {
    if (!this.getSelected()) {
      return;
    }
    this.setSelected(false);
    this.nodeUnselect.emit(this.getId());
  }

  @Method()
  async check() {
    if (this.getChecked()) {
      return;
    }
    this.setChecked(true);
    this.nodeCheck.emit(this.getId());
  }

  @Method()
  async uncheck() {
    if (!this.getChecked()) {
      return;
    }
    this.setChecked(false);
    this.nodeUncheck.emit(this.getId());
  }

  @Method()
  async expand() {
    await this.setExpanded(true);
  }

  @Method()
  async collapse() {
    await this.setExpanded(false);
  }

  hasChild(): boolean {
    return !!this.getChild()?.length;
  }

  renderExpandIcon() {
    if (this.searchText) return;

    if (!this.hasChild() || (this.getExpanded() && this.tree.collapseIcon === '') || (!this.getExpanded() && this.tree.expandIcon === '')) {
      return this.tree.expandIconAlign === 'left' ? <span class="indent" /> : undefined;
    }
    const classes: any = {
      'mb-1': true,
      'mx-1': true,
      'position-absolute': this.tree.expandIconAlign === 'right',
      'end-0': this.tree.expandIconAlign === 'right',
    };
    return (
      <span
        class={classes}
        onClick={ev => {
          ev.stopPropagation();
          this.expandToggle();
        }}
      >
        <FnIcon>{this.getExpanded() ? this.tree.collapseIcon : this.tree.expandIcon}</FnIcon>
      </span>
    );
  }

  renderIndent() {
    if (this.searchText) return;

    const indent = [];
    const level = getFieldValue(this.dataSet, this.tree.fieldLevel) as number;
    for (let i = 1; i < level; i++) {
      indent.push(<span class="indent" />);
    }
    if (!this.hasChild() && this.tree.expandIconAlign === 'left') {
      indent.push(<span class="leaf" />);
    }
    return indent;
  }

  renderChild(): HTMLStormTreeNodeElement {
    if (this.searchText) return;
    if (!this.hasChild() || !this.getExpanded()) return;

    this._child = this.getChild().map(item => {
      return this.renderNodeItem(item);
    });
    return <div class="list-group">{this._child}</div>;
  }

  renderNodeItem(item: object): HTMLStormTreeNodeElement {
    let content = getFieldValue(item, this.tree.fieldLabel) as string;
    if (content && this.searchText) {
      content = appendHighlight(content, this.searchText);
    }

    const node = <storm-tree-node tree={this.tree} dataSet={item} content={content} searchText={this.searchText}></storm-tree-node>;
    return node;
  }

  renderLabel() {
    return <span innerHTML={this.content}></span>;
  }

  componentWillLoad() {
    setFieldValue(this.dataSet, this.tree.fieldElement, this);
  }

  renderCheckbox() {
    if (!this.getCheckSelectable()) return;
    return (
      <storm-checkbox
        type="checkbox"
        isInline={true}
        checked={this.getChecked()}
        onValueChanged={ev => {
          ev.stopPropagation();
        }}
        onClick={async ev => {
          ev.stopPropagation();
          if (this.tree.selectToCheck) {
            this.nodeClick.emit(this.getId());
          } else if (!this.getChecked()) {
            await this.check();
          } else {
            await this.uncheck();
          }
        }}
      ></storm-checkbox>
    );
  }

  render() {
    if (!this.dataSet) return;
    return (
      <Host>
        <a
          href={this.getHref()}
          class={{
            'py-1': true,
            'px-1': true,
            'list-group-item': true,
            'list-group-item-action': getFieldValue(this.dataSet, this.tree.fieldSelectable) as boolean,
            'list-group-item-primary': this.tree.color == 'primary',
            'list-group-item-secondary': this.tree.color == 'secondary',
            'list-group-item-success': this.tree.color == 'success',
            'list-group-item-danger': this.tree.color == 'danger',
            'list-group-item-warning': this.tree.color == 'warning',
            'list-group-item-info': this.tree.color == 'info',
            'list-group-item-light': this.tree.color == 'light',
            'list-group-item-dark': this.tree.color == 'dark',
            'd-flex': true,
            'align-items-end': true,
            active: this.getSelected(),
            disabled: this.getDisabled(),
          }}
          onClick={ev => {
            ev.stopPropagation();
            this.nodeClick.emit(this.getId());
          }}
          onDblClick={ev => {
            ev.stopPropagation();
            this.nodeDblClick.emit(this.getId());
          }}
        >
          {this.renderIndent()}
          {this.renderExpandIcon()}
          <FnIcon>{getFieldValue(this.dataSet, this.tree.fieldIcon) as string}</FnIcon>
          {this.renderCheckbox()}
          {this.renderLabel()}
        </a>
        {this.renderChild()}
      </Host>
    );
  }

  disconnectedCallback() {
    setFieldValue(this.dataSet, this.tree.fieldElement, undefined);
  }
}
